import css from "../css/main.css";
import { Modal, Button, ListGroup } from "react-bootstrap";
import { useState, useEffect } from "react";

const CountriesList = ({ countries }) => {
  const [show, setShow] = useState(false);
  const [capital, setCapital] = useState("");
  const [currency, setCurrency] = useState("");
  const [phone, setPhone] = useState("");
  const [population, setPopulation] = useState("");
  const [countryName, setcountryName] = useState("");

  const handleClose = () => setShow(false);

  let OpenModal = (id, e) => {
    let country = countries.find((country) => country.id === id);
    setCapital(country.capital);
    setCurrency(country.currency);
    setPhone(country.phone);
    setPopulation(country.population);
    setcountryName(country.name);
    setShow(true);
  };

  return (
    <div className="container">
      <div className="row">
        {countries.map((countries) => (
          <div className="col-3 col-md-3 col-12 mt-2" key={countries.id}>
            <div className="card">
              <img
                src={countries.media && countries.media.flag}
                className="card-img-top"
                alt="..."
              />
              <div className="card-body">
                <h5 className="card-title">{countries.name}</h5>
                <a
                  onClick={() => OpenModal(countries.id)}
                  className="btn btn-primary"
                >
                  More Information
                </a>
              </div>
            </div>
          </div>
        ))}
      </div>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header>
          <Modal.Title>{countryName}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <strong>Capital: {capital}</strong>
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Currency: {currency}</strong>
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Phone: {phone}</strong>
            </ListGroup.Item>
            <ListGroup.Item>
              <strong>Population: {population}</strong>
            </ListGroup.Item>
          </ListGroup>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default CountriesList;
